body {
  background-color: var(--tg-theme-secondary-bg-color);
  font-family: Inter, sans-serif;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  user-select: none;
}

::-webkit-scrollbar {
  display: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}