@import "@react-aria/example-theme";

:root {
  --text-color: var(--tg-theme-text-color);
  --text-color-base: var(--tg-theme-hint-color);
  --text-color-hover: var(--highlight-background);
  --text-color-disabled: var(--tg-theme-destructive-text-color);
  --highlight-background: var(--tg-theme-button-color);
  --border-color: var(--tg-theme-section-separator-color);
  --color-primary: var(--tg-theme-button-color);
  --color-good: #31a357;
  --color-bad: #ff5252;
  --focus-ring-color: var(--highlight-background);
}

.container {
  text-align: center;
  margin: 0 auto;
  overflow-x: hidden;
  max-width: 480px;
  padding-left: 16px;
  padding-right: 16px;
  padding-top: 32px;
}

/* Стили вкладок */
.react-aria-Tabs {
  color: var(--tg-theme-hint-color);
  cursor: pointer;
  display: flex;
  flex-grow: 1;
  gap: 4px;
  outline: none;
  position: relative;
  text-decoration: none;
  transition: opacity .2s ease-in-out;
  width: 100%;

  &[data-orientation=horizontal] {
    flex-direction: column;
  }
}

.react-aria-TabList {
  min-width: 100%;
  display: flex;

  &[data-orientation=horizontal] {
    border-bottom: 1px solid var(--border-color);
  }
}

.react-aria-Tab {
  cursor: pointer;
  display: flex;
  flex-grow: 1;
  gap: 4px;
  justify-content: center;
  outline: none;
  padding: 12px;
  position: relative;
  text-decoration: none;
  transition: opacity .2s ease-in-out;
  width: 100%;
  border-bottom: 2px solid transparent;

}

/* Цвета подчеркивания вкладок */
.react-aria-Tab[data-selected][data-key="All"] {
  border-color: var(--highlight-background);
  color: var(--highlight-background);
}

.react-aria-Tab[data-selected][data-key="positive"] {
  border-color: var(--color-good);
  color: var(--color-good);
}

.react-aria-Tab[data-selected][data-key="negative"] {
  border-color: var(--color-bad);
  color: var(--color-bad);
}

.react-aria-TabPanel {
  margin-top: 4px;
  border-radius: 4px;
  outline: none;

  &[data-focus-visible] {
    outline: 2px solid var(--focus-ring-color);
  }
}

.username {
  font-weight: bold;
  margin-top: 32px;
  color: var(--text-color);
}

.registration {
  color: var(--tg-theme-hint-color);
  font-size: 12px;
  background-color: var(--tg-theme-section-bg-color);
  padding: 1px 5px;
  border-radius: 12px;
  display: inline-block;
}

.review {
  display: flex;
  align-items: flex-start;
  border-bottom: 1px solid var(--border-color);
  padding: 10px 0;
}

.review-avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: var(--tg-theme-section-bg-color);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  margin-right: 10px;
  color: var(--text-color);
  flex-shrink: 0;
}

.review-content {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 4px;
  text-align: left;
}

/* Новый блок для выравнивания автора и типа */
.review-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.review-author {
  font-weight: bold;
  color: var(--text-color);
}

.review-status {
  font-size: 13px;
  font-weight: 400;
  line-height: 18px;
  /* Для положительных и отрицательных отзывов будут разные цвета */

  text-align: right; /* или можно оставить это правило, если требуется */
}

.review-positive .review-status {
  color: var(--color-good);
}

.review-negative .review-status {
  color: var(--color-bad);
}

.review-text {
  color: var(--text-color-base);
  font-size: 15px;
  font-weight: 400;
  line-height: 22px;

}